.App {
	font-family: Helvetica;
	width: 100vw;
	height: 100vh;
	max-width: 100%;
}

@media only screen and (min-width: 1200px) {
	#headshot {
		width: 80px;
		height: 80px;
	}

	#statement {
		font-size: 15.2px;
	}
}

@media only screen and (max-width: 1200px) {
	#first, #second {
		font-size: 55px;
	}

	#statement {
		font-size: 14px;
		width: 380px;
	}

	#headshot {
		margin-right: 15px;
		margin-bottom: 15px;
		width: 70px;
  		height: 70px;
	}

	#cardViewer h1 {
		font-size: 15px;
	}

	#cardViewer h2 {
		font-size: 12px;
	}

	#github-icon {
		font-size: 10px;
		bottom: 4%;
	}

	#resume-label {
		font-size: 16px;
	}

	#github-label {
		font-size: 16px;
	}

}

@media only screen and (min-width: 900px) {
	.App {
		display: grid;
		grid-template-columns: 40% 60%;
		grid-template-areas: "info cards";
	}
}


@media only screen and (max-width: 900px) {
	.App {
		display: grid;
		grid-template-rows: 30% 70%;
		grid-template-areas: 
			"info"
			"cards";
		align-items: center;
	}

	#project-title {
		font-size: 8px;
		bottom: 5%;
	}

	#cardViewer {
		position: absolute;
		top: 50px;
		padding-top: 0;
		padding-bottom: 40px;
		right: 0%;
	}

	.card {
		margin-top: 25%;
		width: 90%;
		right: 0;
	}

	#github-icon {
		font-size: 9px;
		bottom: 6%;
	}

	#personal-info {
		position: absolute;
		top: 0;
		left: 50%;
        margin-right: -50%;
        transform: translate(-50%, 0%);
		margin-left: 5px;
		margin-right: 5px;
	}

	#personal-info-container {
		top: 0;
		background-color: #F9FFFD;
		border-bottom: 1px solid lightgray;
		height: 200px;
		width: 100%;
		z-index: 2;
		padding-bottom: 10px;
	}

	#statement {
		margin-top: 15px;
		font-size: 12px;
		width: 450px;
	}

	#info-github-icon {
		width: 20px;
	}

	#file-icon {
		width: 15px;
	}

	#links {
		margin-top: -5px;
	}

	#headshot {
		margin-top: 15px;
		margin-left: 5px;
		width: 50px;
  		height: 50px;
  	}

  	#first {
		margin-left: .5%;
	}

	#first, #second {
		font-size: 30px;
		margin-left: 10px;
	}

	#name-headshot {
		margin-top: 5%;
	}
}


@media only screen and (max-width: 600px) {
	#statement {
		width: 320px;
	}

	#personal-info-container {
		height: 220px;
	}

	#headshot {
		margin-bottom: 2px;
	}

	#info-github-icon {
		width: 20px;
	}

	#file-icon {
		width: 15px;
	}

	#project-title {
		font-size: 8px;
		bottom: 3%;
	}

	#github-icon {
		font-size: 7px;
		bottom: 4.5%;
	}

	#cardViewer {
		position: absolute;
		top: 130px;
	}

	#project-title h1, #project-title h2 {
		font-size: 10px;
	}
}